/*
 * canvas 绘制动画
 * icon: image:// | circle
 * color
 */

<template>
  <canvas ref='canvas' :height="height" :width="width" class="canvas-animate-target"></canvas>
</template>

<script>

const PI = Math.PI
const MR = 0.8
const DI = 50
const IV = 4
const RAF = 'requestAnimationFrame'
const CAF = 'cancelAnimationFrame'
const _16to10 = v => {
  return 'rgba(' + v
    .match(/^#([\da-fA-F]{2})([\da-fA-F]{2})([\da-fA-F]{2})$/i)
    .splice(1, 3)
    .map(c => parseInt(c, 16))
    .join(',') + ','
}

export default {
  props: {
    icon: {
      type: String,
      required: true
    },
    color: {
      type: String,
      default: '#ff0000'
    },
    width: {
      type: [Number, String],
      default: 100
    },
    height: {
      type: [Number, String],
      default: 100
    },
    speed: {
      type: [Number, String],
      default: 4000
    }
  },
  data () {
    return {
      animation: false,
      i: 50,
      img: false,
      c: {},
      tm: 0
    }
  },
  beforeCreate: function () {
    if (!(RAF in window)) {
      window[RAF] = function (fn) {
        return setTimeout(fn, 1000 / 60)
      }
    }
    if (!(CAF in window)) {
      window[CAF] = clearTimeout
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.init()
    })
  },
  beforeDestroy: function () {
    this.animation = false
  },
  methods: {
    init: function () {
      let canvas = this.$refs.canvas
      if (!canvas) return
      let { width, height, icon, color } = this.$props
      let ctx = canvas.getContext('2d')
      let nC = _16to10(color)
      this.animation = true
      this.c = {
        ctx,
        w: width,
        h: height,
        icon,
        color: nC
      }
      this.drawAnimate()
    },
    drawAnimate: function () {
      let { animation } = this
      if (!animation) return
      this.draw()
    },
    draw: function () {
      let tm = new Date().getTime()
      let dt = tm - this.tm
      this.i += this.tm ? dt / this.$props.speed * DI : 1
      this.tm = tm
      let { ctx, w, h, icon } = this.c
      let { i } = this
      let _type_ = icon.substr(0, 5)
      ctx.clearRect(0, 0, w, h)
      let dc = Math.floor(DI / IV)
      switch (_type_) {
        case 'image':
          let { img } = this
          if (!img) {
            img = document.createElement('img')
            img.src = icon.substr(8)
            img.onload = () => {
              this.img = img
              this.drawAnimate()
            }
            return
          }
          for (let n = 0; n < IV; n++) {
            this.drawImg(i - dc * n, img, img.width, img.height)
          }
          this.drawImg(0, img, img.width, img.height)
          break
        case 'rotat':
          this.drawRotate(i)
          break
        default:
          ctx.lineWidth = 2
          for (let n = 0; n < IV; n++) {
            this.drawPolygon(_type_, i - dc * n)
          }
          break
      }
      window[RAF](this.drawAnimate)
    },
    drawImg: function (j, img, ww, hh) {
      let { ctx, w, h } = this.c
      j = j ? (j - 1) % DI : 0
      ctx.globalAlpha = 1 - j / DI
      let sc = 1 + j / DI * 0.5
      ctx.drawImage(img, (w - ww * sc) / 2, (h - hh * sc) / 2, ww * sc, hh * sc)
    },
    drawPolygon: function (_type_, j) {
      let { ctx, w, h, icon, color } = this.c
      let s = Math.min(w, h) / 2
      let x = w / 2, y = h / 2
      let star = false
      if (_type_ === 'stars') {
        star = true
        _type_ = 'polyg'
      }
      ctx.beginPath()
      j = j % DI
      ctx.strokeStyle = color + (1 - j / DI) + ')'
      switch (_type_) {
        case 'circl':
          ctx.arc(x, y, s * MR * (j / DI), 0, PI * 2)
          ctx.stroke()
          break
        case 'polyg':
          let k = icon.split('-')[1]
          let R = j * 0.6
          let r = j
          let at = 360 / k
          let cc = at / 2
          let aft = 90
          let bft = aft + cc
          for (let m = 0; m < k; m++) {
            ctx[m ? 'lineTo' : 'moveTo'](Math.cos((aft + at * m) / 180 * PI) * r + x, -1 * Math.sin((aft + at * m) / 180 * PI) * r + y)
            if (!star) continue
            ctx.lineTo(Math.cos((bft + at * m) / 180 * PI) * R + x, -1 * Math.sin((bft + at * m) / 180 * PI) * R + y)
          }
          ctx.closePath()
          ctx.stroke()
          break
        default: break
      }
    },
    drawRotate: function (j) {
      let { ctx, w, h, icon, color } = this.c
      let s = Math.min(w, h) / 2
      let x = w / 2, y = h / 2
      let k = icon.split('-')[1]
      if (!this.jj) this.jj = 1
      this.jj = j
      ctx.fillStyle = color + '0.9)'
      for (let m = 0; m < k; m++) {
        let arcc = (1 / k * m + j / DI) * 2 * PI
        ctx.beginPath()
        ctx.arc(Math.cos(arcc) * s * 0.8 + x, -1 * Math.sin(arcc) * s * 0.8 + y, s * 0.1, 0, PI * 2)
        ctx.fill()
      }
    }
  },
  watch: {
    icon: function (v) {
      this.init()
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
@import "./index.less";
</style>
